.main   {
  padding: 334px 0 550px;
  background: url(../img/main.svg) no-repeat center bottom / 1950px auto;
  min-height: 1234px;
  p{
    font-size: 24px;
    line-height: 28px;
    color: #fff;
    max-width: 840px;

  }
}

@media(min-width: 1950px){
  .main   {
    min-height: 1334px;
    background: url(../img/main.svg) no-repeat right bottom / 100% auto;
  }
}

.contest{
  margin-top: -50px;
}
.contest__list {
  display: flex;
  flex-wrap: wrap;
  margin-right: -90px;
}

.contest__item {
  width: calc(25% - 90px);
  margin-right: 90px;
}

.contest__pic{
  display: flex;
  justify-content: center;
  margin-bottom: 45px;
}

.contest__text{
  font-size: 26px;
  line-height: 30px;
  text-align: center;
}

.organization{
  padding-top: 210px;
  padding-bottom: 80px;
  background: url(../img/organization-bg.svg) no-repeat center bottom  / 2188px auto;
}

.organization__list {
  display: flex;
  justify-content: space-between;
  margin-bottom: 260px;
}

.organization__item{
  width: calc(33.33% - 30px);
  background-color: #fff;
  height: 213px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-right: 20px;
  padding-left: 40px;
}

.organization__pic{
  margin-right: 40px;
  width: 120px;
}

.organization__text{
  font-size: 20px;
  line-height: 23px;
  width: calc(100% - 160px);
}



@media(max-width: 1400px){
  .main{
    background: url(../img/main.svg) no-repeat center left -200px / 1950px auto;
  }
  .organization__pic{
    margin-right: 20px;
  }
  .organization__item{
    padding-left: 20px;
  }
}


@media(max-width: 1200px){
  .contest__list{
    margin-right: 0px;
    justify-content: space-between;
  }
  .contest__item{
    width: calc(50% - 20px);
    margin-right: 0px;
    margin-bottom: 36px;
  }
  .organization__list{
    display: block;
    font-size: 0px;
    flex-wrap: wrap;
    white-space: nowrap;
    overflow: auto;
    width: calc(100% + 40px);
    margin-left: -20px;
    padding-left: 20px;
    padding-right: 20px;
  }
  .organization__pic{
    display: flex;
    justify-content: center;
    width: 100%;
  }
  .organization__item{
    display: inline-block;
    margin-right: 20px;
    padding-top: 28px;
    padding-bottom: 28px;
    vertical-align: top;
    width: 354px;
    &:last-child{
      margin-right: 0px;
    }
  }
  .organization__text{
    width: 100%;
    text-align: center;
    white-space: normal;
  }
}

@media(max-width: 980px){
  .header{
    padding-top: 34px;
  }
  .header .nav{
    display: none;
  }
  .burger {
    display: block;
  }
  .main{
    padding-top: 120px;
    padding-bottom: 460px;
    min-height: auto;
    background: url(../img/main-mobile.svg) no-repeat center left / auto 100%;
  }
  .mobile-menu{
    background: url(../img/menu-mobile.svg) no-repeat top left -160px / auto 1000px;
    background-color: #fff;
    padding-top: 27px;
  }
  .mobile-menu .nav{
    width: 100%;
    flex-wrap: wrap;
    padding-top: 128px;
    max-width: 262px;
    margin: 0 auto;
  }
  .mobile-menu .nav__link{
    margin-right: 0px;
    text-align: center;
    display: block;
    width: 100%;
    margin-bottom: 36px;
    height: 56px;
    text-align: left;
    color: #fff;
  }
  .socials__link{
    margin-right: 11px;
    svg.svgsprite{
      width: 32px;
      height: 32px;
    }
  }
  h1{
    font-size: 38px;
    line-height: 46px;
    max-width: 300px;
    margin-bottom: 31px;
  }
  .main p{
    font-size: 16px;
    line-height: 19px;
    max-width: 300px;
  }
  h2{
    font-weight: bold;
    font-size: 36px;
    line-height: 44px;
    margin-bottom: 46px;
  }
}

@media(max-width: 800px){
  .footer .nav{
    width: 100%;
    order: -1;
    justify-content: center;
    flex-wrap: wrap;
  }
  .footer .nav__link{
    margin-right: 0px;
    text-align: center;
    display: block;
    width: 100%;
    margin-bottom: 53px;
  }
  .organization{
    padding-bottom: 50px;
    background: url(../img/organization-bg-mobile.svg) no-repeat left -430px bottom  / 2035px auto;
  }
}

@media(max-width: 600px){
  .footer .socials{
    justify-content: center;
    width: 100%;
  }
  .organization__item{
    width: 300px;
  }
  .contest__pic {
    margin-bottom: 24px;
  }
  .contest__pic img{
    width: 226px;
  }
  .contest__text{
    font-size: 24px;
  }
  .contest__item{
    width: 100%;
    margin-right: 0px;
    margin-bottom: 36px;
  }
  .main{
    background: url(../img/main-mobile.svg) no-repeat center left -150px / auto 100%;
  }
}